import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '@/views/home.vue'
import My from '@/views/my.vue'
import Intro from '@/views/Intro.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/info',
  },
  {
    path: '/info',
    component: Intro,
  },
  {
    path: '/home',
    component: Home,
  },
  {
    path: '/my',
    component: My,
  },
]

const router = new VueRouter({
  routes,
})

export default router
