<template>
  <div class="home-container">
    <!-- 标题栏 -->
    <van-nav-bar title="跨客" fixed />
    <!-- 文章区域 -->
    <div class="artbox">
      <ArtInfo v-for="item in artList" :key="item.id" :title="item.title" :author="item.aut_name" :comNum="item.comm_count" :date="item.pubdate" :cover="item.cover"></ArtInfo>
    </div>
    <!-- tabbar -->
    <van-tabbar route>
      <van-tabbar-item replace to="/home" icon="home-o">首页</van-tabbar-item>
      <van-tabbar-item replace to="/my" icon="contact">我的</van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
import { getArticle } from '@/api/ArticleAPI.js'
import ArtInfo from '@/components/ArtInfo.vue'
export default {
  name: 'MyHome',
  data() {
    return {
      page: 1,
      limit: 11,
      artList: [],
    }
  },
  created() {
    this.initArticle()
  },
  methods: {
    async initArticle() {
      const { data: res } = await getArticle(this.page, this.limit)
      this.artList = res
    },
  },
  components: {
    ArtInfo,
  },
}
</script>

<style lang="less" scope>
.home-container {
  .artbox {
    padding: 46px 0 50px 0;
  }
  .van-nav-bar {
    background-color: #0164db;
    .van-nav-bar__title {
      color: #fff;
    }
  }
}
</style>
